修厂名称\"\n                         prop=\"company\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"金额(元)\"\n                         prop=\"settlement_price\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"发货状态\"\n                         prop=\"sm_name\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            {{scope.row.audit_status==0 ? '申请中' :scope.row.audit_status==1 ? '发货中' :scope.row.audit_status==2 ? '已收货' : '驳回'}}\n          </template>\n        </el-table-column>\n        <el-table-column label=\"申请时间\"\n                         prop=\"create_time\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click=\"detailVisible=true,detail(scope.row)\">详情</el-button>\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click='remind(scope.row)'\n                       :disabled=\"!scope.row.audit_status==0\">{{scope.row.audit_status==0 ? '提醒发货' :scope.row.audit_status==1 ? '发货中' :scope.row.audit_status==2 ? '已收货' : '驳回'}}</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <el-dialog title=\"详情\"\n                 center\n                 :visible.sync=\"detailVisible\">\n        <table class=\"table\">\n          <tr>\n            <th>油品名称</th>\n\n            <th>数量</th>\n            <th>价格(元)</th>\n            <th>备注</th>\n\n            <th>油品图片</th>\n          </tr>\n          <tr v-for='(item,index) in detailList'\n              :key=\"index\">\n            <td>\n              {{item.materiel}}\n            </td>\n            <td>\n              {{item.num}}\n            </td>\n            <td>\n              {{item.settlement_price}}\n            </td>\n            <td>\n              {{item.remarks}}\n            </td>\n            <td>\n\n              <img :src=\"item.pic\"\n                   ref=\"images\">\n            </td>\n          </tr>\n        </table>\n      </el-dialog>\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      list: [],\n      seCurId: '',\n      token: window.sessionStorage.getItem('bbytoken'), //token令牌\n      authList: [],\n      page: 0,  //供应商列表当前页数\n      pageCount: 0, //供应商列表总页数\n      detailVisible: false,\n      status: '',\n      detailList: []\n    }\n  },\n\n  methods: {\n    paging (e) {  //获取供应商列表当前页\n      this.page = e\n      this.init()\n    },\n    search () {  //搜索\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/getOilDeliverList', { token: this.token, page: this.page, status: this.status })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows || 0\n\n\n      } catch (err) {\n        throw (err)\n      }\n    },\n    async detail (row) {  //油品详情\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/getOilDetail', { token: this.token, type: row.type, id: row.id, sm_id: row.sm_id })\n        if (res.data.code == 1) {\n          this.detailList = res.data.data\n          this.$nextTick(() => {\n            const ViewerRef = this.$refs.images\n            Viewer(ViewerRef)\n          })\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //提醒发货\n    async remind (row) {\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/remindDeliver', { token: this.token, sm_id: row.sm_id })\n        if (res.data.code == 1) {\n          this.$message({ message: res.data.msg, type: 'success' })\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n\n    // 权限列表,当前所在权限页  \n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data || [];\n          this.authList = arr;\n          console.log(this.authList)\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  created () {\n    this.erAuth()\n  },\n  mounted () {\n\n    this.init() //初始化数据  \n\n  },\n}\n</script>\n<style scoped>\n.search {\n  width: 100%;\n  overflow: hidden;\n}\n.search {\n  padding: 0px 0px 20px 50px;\n}\n\n.table img {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n}\n.ellipsis {\n  width: 80px !important;\n  white-space: nowrap !important;\n  text-overflow: ellipsis !important;\n  overflow: hidden !important;\n}\n</style>"],"sourceRoot":""}]);

